ï»¿@error: #e01a1a;
@success: #07a407;
@white: #fff;
@offwhite: #eee;

body {
    padding-top: 50px;
    padding-bottom: 20px;
}

/* Set padding to keep content from hitting the edges */
.body-content {
    padding-left: 15px;
    padding-right: 15px;
}

.navbar-inverse {
    /*background-color: #602a2a;*/
    background: linear-gradient(#602a2a, #300);
}

/* Override the default bootstrap behavior where horizontal description lists 
   will truncate terms that are too long to fit in the left column 
*/
.dl-horizontal dt {
    white-space: normal;
}

/* Set width on the form input elements since they're 100% wide by default */
input,
select,
textarea {
    max-width: 280px;
}

/* Editing forms */
.error {
    color: @error;
}

.action-successful {
    color: @success;
}

.interface-warning {
    font-size: 2.0em;
    color: @error;
    text-align: center;
}

/* Custom Table Formatting */
.index-buttons {
    width: 60px;
}

.table-standard {
    border: solid 1px #000;

    th, td {
        padding:3px;
    }
}

.table-standard tr:nth-child(odd) {
    background-color: @white;
}

.table-standard tr:nth-child(even) {
    background-color: @offwhite;
}

.ajax-loader {
    font-size: 2em;
    /*display: none;*/
    position: absolute;
    width: 100%;
    height: 600px;
    left: 0;
    top: 0;
    text-align: center;
}

/* Autocomplete */
.ui-autocomplete-loading {
    background: white
                url('data:image/gif;base64,')
                right center no-repeat;
}

.ui-autocomplete { z-index:2147483647; }

.ui-helper-hidden-accessible { position: absolute; left:-999em; }

/* Page Specific */
#firstChartTableHeading, #secondChartTableHeading {
    font-size: 1.25em;
    font-style: italic;
}

/* Page Specific (for Media) */
@media (min-width: 992px) {
    #newAsteroidCoordinatesModal .modal-dialog {
        width: 750px;
    }

    #createAspectInterpretationModal .modal-dialog, #editAspectInterpretationModal .modal-dialog {
        width: 950px;
    }
}

/* Angular cloaking */
[ng\:cloak], [ng-cloak], .ng-cloak, [data\:ng\:cloak], [data-ng-cloak], .data-ng-cloak {
    display: none !important;
}
